.ld-ext-right, .ld-ext-left, .ld-ext-bottom, .ld-ext-top, .ld-over, .ld-over-inverse, .ld-over-full, .ld-over-full-inverse {
  position: relative;
  transition: visibility 0.3s ease-in;
}
.ld-ext-right > .ld, .ld-ext-left > .ld, .ld-ext-bottom > .ld, .ld-ext-top > .ld, .ld-over > .ld, .ld-over-inverse > .ld, .ld-over-full > .ld, .ld-over-full-inverse > .ld {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 1em;
  height: 1em;
  margin: -0.5em;
  opacity: 0;
  z-index: -1;
  transition: visibility 0.3s ease-in;
}
.ld-ext-right > .ld > *, .ld-ext-left > .ld > *, .ld-ext-bottom > .ld > *, .ld-ext-top > .ld > *, .ld-over > .ld > *, .ld-over-inverse > .ld > *, .ld-over-full > .ld > *, .ld-over-full-inverse > .ld > * {
  width: 1em;
  height: 1em;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-0.5em, -0.5em);
}
.ld-ext-right.running > .ld, .ld-ext-left.running > .ld, .ld-ext-bottom.running > .ld, .ld-ext-top.running > .ld, .ld-over.running > .ld, .ld-over-inverse.running > .ld, .ld-over-full.running > .ld, .ld-over-full-inverse.running > .ld {
  opacity: 1;
  z-index: auto;
  visibility: visible;
}

.ld-ext-right.running {
  padding-right: 2.5em !important;
}

.ld-ext-right > .ld {
  top: 50%;
  left: auto;
  right: 1.25em;
}

.ld-ext-left.running {
  padding-left: 2.5em !important;
}

.ld-ext-left > .ld {
  top: 50%;
  right: auto;
  left: 1.25em;
}

.ld-ball, .ld-ring, .ld-hourglass, .ld-loader, .ld-spinner {
  width: 1em;
  height: 1em;
  position: relative;
  color: inherit;
  display: inline-block;
  box-sizing: content-box;
}
.ld-ball:after, .ld-ring:after, .ld-hourglass:after, .ld-loader:after, .ld-spinner:after {
  content: " ";
  display: block;
  width: 2em;
  height: 2em;
  box-sizing: border-box;
  transform-origin: 0em 0em;
  transform: translateZ(0) scale(0.5);
  backface-visibility: hidden;
}

.ld-ring:after {
  border-radius: 50%;
  border: 0.3em solid currentColor;
  border-left-color: transparent;
}

.ld-hourglass:after {
  border-radius: 50%;
  border: 1em solid currentColor;
  border-left-color: transparent;
  border-right-color: transparent;
}

.ld-spinner:after {
  position: absolute;
  width: 0.4em;
  height: 0.4em;
  transform: translateZ(0) scale(0.5) translate(0.8em, 0.8em);
  border-radius: 50%;
  background: none;
  box-shadow: 0 1em 0 currentColor, 0 -1em 0 currentColor, 1em 0 0 currentColor, -1em 0 0 currentColor, 0.70710678em 0.70710678em 0 currentColor, -0.70710678em 0.70710678em 0 currentColor, 0.70710678em -0.70710678em 0 currentColor, -0.70710678em -0.70710678em 0 currentColor;
}

.ld-loader {
  background-size: cover;
}

.ld {
  transform-origin: 50% 50%;
  transform-box: fill-box;
}

@keyframes ld-spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.ld.ld-spin {
  animation: ld-spin 0.7s infinite linear;
}

html {
  scroll-behavior: smooth;
}

